body {
  background-color: #f4f4f9;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  font-family: Arial, sans-serif;
  display: flex;
}

.cat-container {
  text-align: center;
  background: #fff;
  border-radius: 15px;
  flex-direction: column;
  align-items: center;
  max-width: 600px;
  padding: 20px;
  display: flex;
  box-shadow: 0 10px 20px #0000001a;
}

.cat-image {
  border-radius: 15px;
  width: 100%;
  max-width: 400px;
  margin-bottom: 20px;
  box-shadow: 0 5px 15px #0003;
}

.cat-details h2 {
  color: #2c3e50;
  margin-bottom: 10px;
  font-size: 24px;
}

.cat-details p {
  color: #7f8c8d;
  margin: 5px 0;
  font-size: 16px;
  line-height: 1.6;
}

.cat-details p strong {
  color: #34495e;
}

.breed-select {
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 10px;
  width: 100%;
  max-width: 400px;
  margin-bottom: 20px;
  padding: 10px 15px;
  font-size: 16px;
  box-shadow: 0 3px 5px #0000001a;
}

.loader {
  color: #3498db;
  text-align: center;
  margin-top: 20px;
  font-size: 18px;
}

.error {
  color: #e74c3c;
  text-align: center;
  margin-top: 20px;
  font-size: 16px;
  font-weight: bold;
}

.cat-container {
  animation: .5s ease-in-out fadeIn;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

